@use 'foundation' as *;

.drawer {
  width: 100%;
  height: 100vh;
  background:#282F35;
  overflow-y: scroll;
  transition: .6s;
}

.drawer__nav-items {
  // padding: prem(100) 0;
}

.drawer__nav-item a {
  padding: prem(10) 0;
  display: block;
  font-size: prem(16);
  font-family: $font-family-en;
  font-style: italic;
  line-height: 1;
  letter-spacing: 0.05em;
  text-align: center;
  text-transform: uppercase;
}


.body-locked {
  height: 100%;
  position: fixed;
}
